<template>
  <v-footer
    :fixed="true"
    app
    dark
    color="primary"
    flat>
    <!-- <span>&copy; 2018</span> -->
    <iframe frameborder="0" :src="widgetSource" height="250%" width="100%" scrolling="no"></iframe>
  </v-footer>
</template>
<script>
export default {
  beforeMount () {

  },
  computed: {
    widgetSource: {
      get () {
        return `${this.source}/?h=100&w=${this.width}`
      }
    }
  },
  data () {
    return {
      source: 'https://indianotes.com/en/widgets-offerings/widgets/indices-ticker',
      width: window.outerWidth
    }
  }
}
</script>



